@import url(./rating.css);

.page {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #060b35;
}

.card {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
}

.card__photo {
  margin-bottom: 40px;
}

.card__title {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  margin-bottom: 4px;
}

.card__code {
  margin-bottom: 16px;
  color: #616070;
}

.card__rating {
  margin-bottom: 24px;
}

.card__pricetag {
  display: flex;
  justify-content: space-between;
  line-height: 18px;
  margin-bottom: 16px;
}

.pricetag_text {
  font-size: 12px;
  color: #616070;
}

.pricetag__price {
  font-weight: 700;
  font-size: 16px;
}

.card__buy-button {
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 38px;
  color: #fff;
  background: #00acdc;
  border-radius: 5px;
  text-align: center;
  text-transform: uppercase;
  border: 1px solid transparent;
}

.card__buy-button:hover {
  color: #00acdc;
  border-color: #00acdc;
  background-color: #fff;
}
